}

.ozbeanz-bg {
  background: url(../images/ozbeanz-bg.png) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.microsoft-bg {
  background: url(../images/microsoft-bg.png) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

[class*=" bmd-label"], [class^=bmd-label] {
